@use "styles/variables" as var;

.todo {
  background-color: var.$white;
  border-radius: var.$borderRadius;
  padding: 1.1rem;
  margin-bottom: var.$mainSpacing;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  // cursor: pointer;

  .form {
    height: 100%;
    select {
      height: 100%;
      max-width: 8rem;
    }

    .topBar {
      display: flex;
      justify-content: space-between;
      margin-bottom: 0.6rem;
      padding-bottom: 0.4rem;
      border-bottom: 0.1rem solid rgba(0, 0, 0, 0.1);

      .priorityContainer {
        display: flex;
        position: relative;
        justify-content: space-between;
        align-items: center;
        font-size: 1.6rem;
        font-weight: 300;
        width: 2.5rem;
        cursor: pointer;

        select {
          -webkit-appearance: none;
          -moz-appearance: none;
          appearance: none;
          border: none;
          cursor: pointer;
          width: 100%;
          color: var.$mainBg;

          &::-ms-expand {
            display: none;
          }
        }

        svg {
          width: 1.4rem;
          height: 1.4rem;
          margin-left: 0.2rem;
          position: absolute;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
          display: block;
          pointer-events: none;
        }
      }

      .topBar_right {
        display: flex;

        .deleteBtn {
          width: 2rem;
          height: 2rem;
          margin-left: 1rem;
          cursor: pointer;

          svg {
            width: 100%;
            height: 100%;
          }
        }
      }
    }

    .title {
      font-size: var.$fontSizeText;
      color: var.$black;
      line-height: 1.7rem;
      cursor: pointer;
      width: fit-content;
    }

    .titleInputContainer {
      display: flex;
      justify-content: space-between;
      width: 100%;
      height: fit-content;

      .inputTitle {
        background-color: var.$white;
        border-radius: var.$borderRadius;
        overflow: hidden;
        overflow-wrap: break-word;
        height: 7.4rem;
        border: none;
        padding: 0 0.5rem;
        color: var.$black;
        font-size: 1.4rem;
        font-weight: 400;
        line-height: 2rem;
        overflow: hidden;
        overflow-wrap: break-word;
        resize: none;
        width: 100%;
        box-sizing: border-box;
        border: 0.1rem solid rgba(0, 0, 0, 0.2);
      }

      .formContainer_right {
        display: flex;
        flex-direction: column;
        margin-left: 1rem;
        justify-content: center;

        .addBtn,
        .cancelBtn {
          width: 2.6rem;
          height: 2.6rem;
          cursor: pointer;

          svg {
            width: 100%;
            height: 100%;
          }
        }

        .addBtn {
          margin-bottom: 1rem;
        }
      }
    }
  }
}
